* {
    font-size: 10px;
    padding: 0;
    margin: 0 auto;
    box-sizing: border-box;
    font-family: serif Helvetica;
}

body {
    width: 800px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.info {
    text-align: center;
    h1 {
        font-size: 4rem;
        margin-bottom: 10px;
    }
    p {
        font-size: 2rem;
        margin-bottom: 10px;
    }
    .game-update {
        font-size: 2rem;
        font-weight: bold;
    }
}

.container {
    height: 800px;
    width: 100%;
    background-color: whitesmoke;
}

div {
    border: 3px solid black;
    border-radius: 50%;
    width: 100%;
    height: 100%;
    background:grey;
    display: inline-block;
    transition: 0.51s;
}

.p1 {
    background-color: dodgerblue;
}

.p2 {
    background-color: orangered;
}
